import React from 'react';
import {
	Title,
	Button,
	Div,
	Counter,
	Cell,
	ModalCard
} from '@vkontakte/vkui';
import {
	Icon24AddCircleDottedOutline,
	Icon24SkullOutline,
	Icon24CupOutline,
	Icon24Users3Outline,
	Icon24TshirtOutline,
	Icon24MoneyCircleOutline,
	Icon24PollOutline,
	Icon24FavoriteOutline,
	Icon24StickerOutline,
	Icon24LikeOutline,
	Icon24HammerOutline
} from '@vkontakte/icons';

class MODAL extends React.Component {
	constructor(props) {
		super(props);
		this.state = {};
	};
	async componentDidMount() {
		console.log('[MODAL] >', this.props.id);
	};
	shouldComponentUpdate() {
		return false;
	};
	render() {
		return (
			<ModalCard id={this.props.id}>
				<Div style={{padding: 0}}>
					<Title style={{ margin: '32px 16px', textAlign: 'center' }} level="2" weight="medium"></Title>
					<div style={{ margin: '16px 8px', gap: 8, display: 'flex', flexDirection: 'column'}}>
						<Cell className="DescriptionWiki" before={<Icon24AddCircleDottedOutline />} description="      "></Cell>
						<Cell after={<Counter size="s" mode="prominent"></Counter>} className="DescriptionWiki" before={<Icon24SkullOutline />} description="        2 ">  </Cell>
						<Cell after={<Counter size="s" mode="prominent"></Counter>} className="DescriptionWiki" before={<Icon24CupOutline />} description="         ">  </Cell>
						<Cell after={<Counter size="s" mode="primary"></Counter>} className="DescriptionWiki" before={<Icon24HammerOutline />} description="        "> </Cell>
						<Cell className="DescriptionWiki" before={<Icon24Users3Outline />} description="   ,       "> </Cell>
						<Cell className="DescriptionWiki" before={<Icon24TshirtOutline />} description="          "> </Cell>
						<Cell className="DescriptionWiki" before={<Icon24MoneyCircleOutline />} description="    ">,   </Cell>
						<Cell className="DescriptionWiki" before={<Icon24PollOutline />} description="   , ,      "></Cell>
						{/* <Cell className="DescriptionWiki" before={<Icon24FavoriteOutline />} description="          ">  </Cell> */}
						{/* <Cell className="DescriptionWiki" before={<Icon24StickerOutline />} description="  VK Donut    "></Cell> */}
						{/* <Cell className="DescriptionWiki" before={<Icon24LikeOutline />} description="     "> </Cell> */}
					</div>
					<div style={{display: 'flex', flexDirection: 'column', margin: 16}}>
						<Button href="https://vk.com/donut/wiki.warlord" stretched target="_blank" size="l" mode="primary">  50  </Button>
					</div>
				</Div>
    		</ModalCard>
		);
	};
};
export default MODAL;