import * as React from "react";
import { classNames } from "../../lib/classNames";
import { Paragraph } from "../Typography/Paragraph/Paragraph";
import { Tappable } from "../Tappable/Tappable";
import { hasReactNode } from "../../lib/utils";
import "./MiniInfoCell.css";

export interface MiniInfoCellProps
  extends React.HTMLAttributes<HTMLDivElement> {
  /**
   *  .<br />
   *     20.
   */
  before: React.ReactNode;

  /**
   *  .<br />
   * `<UsersStack size="s" />`  `<Avatar size={24} />`
   */
  after?: React.ReactNode;

  /**
   *  :
   *
   * - `base`         .<br />
   *     `Link`,      .
   * - `add`   ,  ,         -.
   * - `more`        -  .
   */
  mode?: "base" | "add" | "more";

  /**
   *   :
   *
   * - `nowrap`    ,     .
   * - `short`    3 ,  .
   * - `full`    .
   */
  textWrap?: "nowrap" | "short" | "full";

  /**
   *  :
   *
   * - `primary`    ,       .<br /> :     
   * - `secondary`   -.
   */
  textLevel?: "primary" | "secondary";
}

/**
 * @see https://vkcom.github.io/VKUI/#/MiniInfoCell
 */
export const MiniInfoCell = ({
  before,
  after,
  mode = "base",
  textWrap = "nowrap",
  textLevel = "secondary",
  children,
  ...restProps
}: MiniInfoCellProps) => {
  const cellClasses = classNames(
    "MiniInfoCell",
    mode !== "base" && `MiniInfoCell--md-${mode}`,
    textWrap !== "nowrap" && `MiniInfoCell--wr-${textWrap}`,
    `MiniInfoCell--lvl-${textLevel}`
  );

  const cellContent = (
    <React.Fragment>
      <span vkuiClass="MiniInfoCell__icon">{before}</span>
      <Paragraph
        vkuiClass="MiniInfoCell__content"
        weight={mode === "more" ? "2" : undefined}
      >
        {children}
      </Paragraph>
      {hasReactNode(after) && (
        <span vkuiClass="MiniInfoCell__after">{after}</span>
      )}
    </React.Fragment>
  );

  return restProps.onClick ? (
    <Tappable
      Component="div"
      role="button"
      {...restProps}
      vkuiClass={cellClasses}
    >
      {cellContent}
    </Tappable>
  ) : (
    <div {...restProps} vkuiClass={cellClasses}>
      {cellContent}
    </div>
  );
};
